import React, { Component } from 'react';
import { Link, Redirect, Route, Switch } from 'react-router-dom';
import { withRouter } from 'react-router';
import { FaPlay, FaPause } from 'react-icons/fa';
import { Button, Tab, TabId, Tabs } from '@blueprintjs/core';

var synthesisVoices = speechSynthesis.getVoices();

class App extends Component
{
    constructor(props)
    {
        super(props);

        let tabId = 'Workout';
        switch(props.location.pathname.substring(1)) {
            case 'Workout':
            case 'A':
            case 'B':
                tabId = 'Workout';
                break;

            case 'About':
                tabId = 'About';
                break;
        }

        this.state ={
            voicesLoaded: false,
            tabId: tabId,
        };

        this.loadVoices = this.loadVoices.bind(this);
    }

    componentDidMount()
    {
        speechSynthesis.onvoiceschanged = this.loadVoices;
    }

    loadVoices()
    {
        synthesisVoices = speechSynthesis.getVoices();
        this.setState({voicesLoaded: true});
    }

    render()
    {
        const { voicesLoaded } = this.state;
        const { history } = this.props;

        return (
            <div className="App">
                <Tabs className="border-bottom"
                    animate={true}
                    id="navigation"
                    large={true}
                    onChange={(tabId: TabId) => {
                        this.setState({tabId}, () => {
                            history.push('/' + tabId);
                        });
                    }}
                    selectedTabId={this.state.tabId}>
                    <Tab id="Workout">
                        <Link to="/">Workout</Link>
                    </Tab>
                    <Tab id="About">
                        <Link to="/About">About</Link>
                    </Tab>
                </Tabs>

                <div className="viewport">
                    {
                        voicesLoaded === false ?
                            <Loading /> :
                            (
                                <Switch>
                                    <Route component={ (props) => { return <Workout {...props} workout={null} /> } } path="/"  exact />
                                    <Route component={ (props) => { return <Workout {...props} workout={'A'} /> } } path="/a" exact />
                                    <Route component={ (props) => { return <Workout {...props} workout={'B'} /> } } path="/b" exact />
                                    <Route component={ About  } path="/About" exact />

                                    <Redirect to="/" />
                                </Switch>
                            )
                    }
                </div>
            </div>
        );
    }
}

class Workout extends Component
{
    constructor(props)
    {
        super(props);

        this.state = {
            interval: undefined,
            item: 0,
            state: 'not-started',
            timer: timePerExercise + timeBetweenExercises,
        };

        this.countdown = this.countdown.bind(this);
    }

    componentDidMount()
    {
        if (this.props.workout) {
            this.setState({interval: window.setInterval(() => {
                this.countdown();
            }, 1000)});
        }
    }

    componentWillUnmount()
    {
        window.clearInterval(this.state.interval);
    }

    render()
    {
        const { history, match, workout } = this.props;
        const { item, state, timer } = this.state;

        if (workout) {
            const currentWorkoutItem = workoutItems[workout][item];

            let content = null;

            if (['not-started', 'paused'].indexOf(state) !== -1) {
                content = (
                    <Button fill={true}
                        large={true}
                        intent={'primary'}
                        icon={'play'}
                        onClick={() => this.setState({state: 'started'})}>
                        { state === 'not-started' ? 'Get Started' : 'Resume' }
                    </Button>
                );
            } else if (state === 'started') {
                content = (
                    <>
                        <Button fill={true}
                            large={true}
                            intent={'danger'}
                            icon="pause"
                            className="flex-grow-1"
                            onClick={() => this.setState({state: 'paused'})}>
                            Pause
                        </Button>

                        <div className="flex-grow-1">
                            { secondsToTime(timer) }
                        </div>

                        <h2 className="flex-grow-2">
                            { currentWorkoutItem }
                        </h2>

                        <div className="flex-grow-8">
                            <img alt="Workout" src={ `./images/${workout}-${item + 1}.png` } />
                        </div>
                    </>
                );
            } else if (state === 'done') {
                announce('All done. Good workout mother flipper!');

                content = (
                    <>
                        Done!
                    </>
                );
            }

            return (
                <div className="Workout">
                    <header>
                        Workout {workout}
                    </header>

                    { content }
                </div>
            );
        }

        return (
            <div className="Workout">
                <Button fill={true}
                    large={true}
                    intent={'warning'}
                    className="vertical-fill"
                    onClick={() => history.push('/A')}>
                    Workout A
                </Button>
                <Button fill={true}
                    large={true}
                    intent={'danger'}
                    className="vertical-fill"
                    onClick={() => history.push('/B')}>
                    Workout B
                </Button>
            </div>
        );
    }

    countdown()
    {
        const { interval, item, state, timer } = this.state;
        const { workout } = this.props;
        const exercises = workoutItems[workout];

        const newTimer = parseInt(timer - 1, 10);
console.log(state, timer, newTimer, timePerExercise);

        if (['done', 'not-started', 'paused'].indexOf(state) !== -1) {
            return;
        }


        if (newTimer > timePerExercise) {
            this.setState({timer: newTimer});

            return;
        }

        if (newTimer === timePerExercise) {
            announce(exercises[item] + '. Go');
        }
        if (newTimer > 0) {
            this.setState({timer: newTimer}, () => {
                if (Math.floor(timePerExercise / 2) === newTimer) {
                    announce(Math.floor(timePerExercise / 2) + ' seconds left.');
                } else if (newTimer <= 10) {
                    announce(newTimer);
                }
            })
        } else if (newTimer === 0) {
            if (typeof exercises[item + 1] !== 'undefined') {
                this.setState({item: parseInt(item + 1, 10), timer: timePerExercise + timeBetweenExercises}, () => {
                    if (timeBetweenExercises > 3) {
                        announce(timeBetweenExercises + ' second break, then ' + exercises[item + 1]);
                    }
                })
                return;
            }

            this.setState({state: 'done'});
            window.clearInterval(interval);
        }
    }
}

